:root {
  --font-body: "Lato", sans-serif;
  --font-display: "League Spartan", sans-serif;
}

:root {
  --nwcGreen: hsl(167, 100%, 20.2%);
  --nwcLightGreen: hsl(120, 55.5%, 44.9%);
  --nwcRed: hsl(355, 84.5%, 54.5%);
  --nwcYellow: hsl(47, 100%, 46.1%);
  --nwcBlue: hsl(220, 100%, 23.1%);
  --nwcLightBlue: hsl(207, 100%, 40.6%);
  --ncrBlue: hsl(208, 100%, 34%);
  --ncrOrange: hsl(32, 94%, 60%);
  --ncrGreen: hsl(92, 51%, 51%);
  --weBlue: hsl(196, 100%, 47%);
  --green: hsl(166, 100%, 18.4%);
  --lightgreen: hsl(130, 40.5%, 52.5%);
  --fadeRed: hsl(360, 82.5%, 57.5%);
  --brightRed: hsl(358, 72.6%, 45.9%);
  --red: hsl(358, 84.6%, 51.8%);
  --darkBlue: hsl(235, 52.2%, 9%);
  --gold: hsl(54, 69.2%, 84.7%);
  --lightGold: hsl(54, 71.4%, 97.3%);
  --black: hsl(0, 0%, 0%);
  --richBlack: hsl(0, 0%, 6%);
  --lightGrey: hsl(210, 1%, 60.8%);
  --grey: hsl(0, 0%, 50.2%);
  --lightSlateGray: hsl(210, 14.3%, 53.3%);
  --darkSlateGray: hsl(180, 25%, 25%);
  --slateGray: hsl(210, 13%, 50%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
}

.container {
  width: 80%;
  max-width: 75rem;
  margin: 0 auto;
}
.container__content {
  width: 60%;
  margin-bottom: 2.5rem;
}
@media (max-width: 59.999375em) {
  .container__content {
    width: 80%;
  }
}
@media (max-width: 43.74875em) {
  .container__content {
    width: 100%;
  }
}

.row {
  padding: 0 1.25rem;
}

article,
aside,
footer,
header,
nav,
section,
figcaption,
figure,
main {
  display: block;
}

.container {
  width: 80%;
  max-width: 1169px;
  margin: 0 auto;
  position: relative;
}
@media (max-width: 43.74875em) {
  .container {
    width: 100%;
    padding: 0px 15px;
  }
}

.bold {
  font-weight: 700;
  letter-spacing: 1px;
}

img {
  max-width: 100%;
  height: auto;
}

.fade {
  transition: all 300ms ease-in-out;
}

.fade:hover {
  opacity: 60%;
}

.fl-l {
  float: left;
  margin-right: 1.25rem;
  margin-bottom: 1.25rem;
}

.fl-r {
  float: right;
  margin-left: 1.25rem;
  margin-bottom: 1.25rem;
}

.center {
  text-align: center;
  margin-bottom: 1.25rem;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

.tag {
  margin-bottom: 12px;
}

h1 {
  font-family: var(--font-display);
  font-size: 3rem;
  color: var(--black);
  font-weight: 700;
  letter-spacing: 1px;
}

h2 {
  font-family: var(--font-display);
  font-size: 2.5rem;
  color: var(--black);
  font-weight: 700;
  letter-spacing: 1px;
}

h3 {
  font-family: var(--font-display);
  font-size: 2rem;
  color: var(--black);
  font-weight: 700;
  letter-spacing: 1px;
}

h4 {
  font-family: var(--font-display);
  font-size: 1.5rem;
  color: var(--black);
  font-weight: 700;
  letter-spacing: 1px;
}

h5 {
  font-family: var(--font-display);
  font-size: 1.25rem;
  color: var(--black);
  font-weight: 700;
  letter-spacing: 1px;
}

p {
  font-family: var(--font-body);
  font-size: 1rem;
  color: var(--black);
  line-height: 1.5;
  margin-bottom: 1.5rem;
  font-weight: 300;
}

.button {
  text-align: center;
  display: inline-block;
}

.btn {
  font-family: var(--font-display);
  color: var(--white);
  font-weight: 700;
  font-size: 1rem;
  text-align: center;
  padding: 0.5rem 1rem;
  border: 0px;
  border-radius: 0px;
  cursor: pointer;
  letter-spacing: 1px;
}
.btn--dark {
  background-color: var(--nwcBlue);
  border: 1px solid var(--nwcBlue);
}
.btn--dark:hover, .btn--dark:active, .btn--dark:focus {
  background-color: var(--white);
  color: var(--nwcBlue);
  border: 1px solid var(--nwcBlue);
  transition: all 300ms ease-in-out;
}
.btn--dark-footer {
  font-size: 24px;
  font-weight: 500;
  padding: 0.625rem 3.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: block;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-jc-c {
  justify-content: center;
}
.flex-jc-fs {
  justify-content: flex-start;
}
.flex-jc-fe {
  justify-content: flex-end;
}
.flex-ai-fs {
  align-items: flex-start;
}
.flex-ai-c {
  align-items: center;
}
.flex-fd-r {
  flex-direction: row;
}
.flex-fd-c {
  flex-direction: column;
}
.flex-fw-w {
  flex-wrap: wrap;
}
.flex-fw-nw {
  flex-wrap: nowrap;
}

.ourProduct-cards {
  gap: 5%;
  margin: 4.6875rem auto;
}
@media (max-width: 59.999375em) {
  .ourProduct-cards {
    gap: 2%;
    width: 95%;
  }
}

.op-card {
  text-align: left;
  width: 20%;
  padding: 20px 0 10px 0;
  text-align: center;
}
@media (max-width: 59.999375em) {
  .op-card {
    width: 23%;
  }
}
@media (max-width: 43.74875em) {
  .op-card {
    text-align: center;
    width: 45%;
  }
}
.op-card-image {
  max-width: 100%;
  height: auto;
  margin-bottom: 15px;
}
.op-card p {
  font-size: 22px;
  font-weight: 700;
  line-height: 1.4;
  color: var(--nwcBlue);
}

.ourCustomers {
  background: url("./img/ourCustomers2_1526x575-min.jpg") no-repeat;
  background-position: center;
  background-size: cover;
  height: 575px;
  margin-top: 50px;
  margin-bottom: 75px;
}
@media (max-width: 43.74875em) {
  .ourCustomers {
    background: none;
    height: auto;
    opacity: 100%;
  }
}
.ourCustomers-pullOut {
  background-color: rgba(255, 222, 23, 0.85);
  padding: 40px 20px;
  width: 30%;
  margin-right: 25%;
}
@media (max-width: 59.999375em) {
  .ourCustomers-pullOut {
    background-color: rgba(255, 222, 23, 0.85);
    padding: 30px 140px;
    width: 100%;
    margin-right: 0;
  }
}
@media (max-width: 43.74875em) {
  .ourCustomers-pullOut {
    background-color: rgb(255, 222, 23);
    text-align: center;
    padding: 30px 20px;
  }
}
.ourCustomers h2 {
  font-size: 46px;
  font-weight: 300;
  color: var(--nwcBlue);
}
@media (max-width: 43.74875em) {
  .ourCustomers h2 {
    font-size: 32px;
  }
}
.ourCustomers h4 {
  font-size: 24px;
  font-weight: 500;
  color: var(--nwcBlue);
}
@media (max-width: 43.74875em) {
  .ourCustomers h4 {
    font-size: 20px;
  }
}

.ourProducts {
  background: url("./img/ourProducts2_1526x575-min.jpg") no-repeat;
  background-position: center;
  background-size: cover;
  height: 575px;
  margin-top: 50px;
  margin-bottom: 75px;
  margin-left: auto;
}
@media (max-width: 43.74875em) {
  .ourProducts {
    background: none;
    height: auto;
  }
}
.ourProducts-pullOut {
  background-color: rgba(30, 66, 128, 0.75);
  padding: 40px 20px;
  width: 30%;
  margin-left: 25%;
}
@media (max-width: 59.999375em) {
  .ourProducts-pullOut {
    background-color: rgba(30, 66, 128, 0.85);
    padding: 30px 140px;
    width: 100%;
    margin-left: 0;
  }
}
@media (max-width: 43.74875em) {
  .ourProducts-pullOut {
    background-color: rgb(30, 66, 128);
    padding: 30px 10px;
    text-align: center;
  }
}
.ourProducts h2 {
  font-size: 46px;
  font-weight: 300;
  color: var(--white);
}
@media (max-width: 43.74875em) {
  .ourProducts h2 {
    font-size: 32px;
  }
}
.ourProducts h4 {
  font-size: 24px;
  font-weight: 500;
  color: var(--white);
}
@media (max-width: 43.74875em) {
  .ourProducts h4 {
    font-size: 20px;
  }
}

.cta {
  background: linear-gradient(var(--nwcYellow), var(--lightGold));
  height: 325px;
  text-align: center;
}
.cta h2 {
  color: var(--white);
  font-size: 38px;
  margin-bottom: 30px;
}
@media (max-width: 43.74875em) {
  .cta h2 {
    font-size: 32px;
  }
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  font-family: var(--font-body);
  font-size: 1rem;
  line-height: 1.5;
  color: var(--grey);
}

@keyframes no-transform {
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes pulse {
  0% {
    opacity: 1;
    transform: none;
  }
  50% {
    opacity: 0.8;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
.js-loading *,
.js-loading *:before,
.js-loading *:after {
  animation-play-state: paused !important;
}

.header {
  background-color: var(--nwcYellow);
  height: 4.75em;
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.header::before {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8)), url(./img/background_min-1920x778.jpg) no-repeat bottom;
  background-size: cover;
  bottom: 0;
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
}

.header::after {
  background: var(--white);
  content: "";
  height: 20rem;
  left: -5%;
  position: absolute;
  right: -5%;
  top: 90%;
  z-index: 200;
  transform: rotateZ(-4deg);
  transform-origin: 0, 0;
}

.nav {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: var(--nwcYellow);
  z-index: 900;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 10px;
}
@media (max-width: 43.74875em) {
  .navbar {
    justify-content: space-around;
  }
}
.navbar .container {
  margin: 0 30px;
}

.headerLogo {
  margin: 0.625rem 0 0 0.625rem;
}
@media (max-width: 43.74875em) {
  .headerLogo {
    width: 65%;
    margin: 0;
    margin-top: 0.625rem;
    margin-right: 0.625rem;
  }
}

.header-down-arrow {
  animation: pulse 2s 3s ease-out infinite;
  position: absolute;
  bottom: 2vh;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 300;
}

.header-down-arrow img {
  animation: no-transform 1s 1s ease-out forwards;
  opacity: 0;
  transform: translateY(4rem);
}

.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.heroBkgd {
  width: 100%;
}

.hero-bar {
  position: relative;
  background-color: rgba(30, 66, 128, 0.65);
  width: 60%;
  top: 25%;
  display: flex;
  align-items: center;
  z-index: 100;
}
@media (max-width: 59.999375em) {
  .hero-bar {
    width: 100%;
    top: 45%;
  }
}

.heroHeading {
  width: 60%;
  margin-right: 0;
  padding: 20px 20px 20px 0;
}
@media (max-width: 59.999375em) {
  .heroHeading {
    width: 75%;
    text-align: center;
    margin-right: auto;
  }
}
.heroHeading h1 {
  color: var(--white);
}
@media (max-width: 43.74875em) {
  .heroHeading h1 {
    font-size: 2.5rem;
    line-height: 1;
    margin-bottom: 1rem;
  }
}
.heroHeading h3 {
  color: var(--white);
}
@media (max-width: 43.74875em) {
  .heroHeading h3 {
    font-size: 1.35rem;
    line-height: 1.3;
  }
}

@media (max-width: 59.999375em) {
  footer .container {
    text-align: center;
  }
}

.footerLogo img {
  margin: 10px auto;
}
@media (max-width: 43.74875em) {
  .footerLogo {
    width: 80%;
    margin: 20px auto;
  }
}

.copyright {
  text-align: center;
  margin-bottom: 20px;
}
.copyright p {
  font-size: 14px;
  color: var(--grey);
}